	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Racket is the best programming language that embraces the premise that there is no best programming language. In other words, Racket is designed to support the creation of programming languages which can then be used to build programs. Using Racket, we have built program languages ranging from large, general-purpose languages matching particular programming paradigms to small, specialized programming languages designed for just a single file in some larger program.\n\nIn this talk, I will bring across how it feels to program in such a language, discuss some of the technical support Racket has to make such programming practical, and show how Racket’s standard libraries have embraced this perspective, enabling us to include lots of batteries.\n\nRobby is an associate professor of computer science at Northwestern University and received his PhD from Rice University in 2002. He is the author or two textbooks, one on introductory programming and one on operational semantics. Robby is one of the core developers of Racket and has helped shape the design the language since its inception in 1994.\n\nFor more on Lambda Jam, visit http://www.lambdajam.com","meta":{"duration":"PT51M43S"},"presenter":{"name":null},"source":{"id":"hFlIl0Zo234","provider":"youtube"},"slug":"lambda-jam-2015-robby-findler-racket-a-programming-language-programming-language","tag":["Robby Findler","Lambda Jam 2015","Racket","Programming Languages","Functional Programming"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/hFlIl0Zo234/default.jpg","height":90,"width":120}},"title":"Lambda Jam 2015 - Robby Findler - Racket: A Programming-Language Programming Language"}}]}},"pathContext":{"slug":"lambda-jam-2015","tag":"Lambda Jam 2015"}}

